<template>
    <div id="stacked">
        <vue-chart ref="stacked" :options="orgOptions" :auto-resize="true"></vue-chart>
    </div>
</template>

<script>
    import VueChart from 'vue-echarts'
    import 'echarts/lib/chart/pie'
    import 'echarts/lib/component/tooltip'
    import 'echarts/lib/component/title'
    import 'echarts/lib/component/legend'
    import {reqGetOneDayOrderList} from "../../../api";

    export default {
        name: "Stacked2",
        components:{
            VueChart
        },
        data () {
            return {
                orgOptions:{}
            }
        },
        created(){
            this.getOrderListInfo()
        },
        methods:{
            async getOrderListInfo(){
                let todayDate = new Date().toLocaleDateString()
                const result = await reqGetOneDayOrderList({todayDate})
                const {status,data} = result;
                if(status===0){
                    this.initChart(data)
                }
            },
            initChart(ListInfo){
                let initArr = [
                    {name: '', value: 0}, {name: '', value: 0}, {name: '', value: 0},
                    {name: '', value: 0}
                ];
                //,
                ListInfo.forEach(cItem=>{
                    switch(cItem.productType){
                        case '0':
                            (initArr[0]).value += cItem.productCount*(+ cItem.productPrice);
                            break;
                        case '1':
                            initArr[1].value += cItem.productCount*cItem.productPrice;
                            break;
                        case '2':
                            initArr[2].value += cItem.productCount*cItem.productPrice;
                            break;
                        case '3':
                            initArr[3].value += cItem.productCount*cItem.productPrice;
                            break;
                    }
                })
                this.orgOptions = Object.freeze({   //,Observer
                    title: {
                        text: ',',
                        left: 'center',
                        top: 20,
                        textStyle: {
                            color: '#42B983'
                        }
                    },
                    tooltip: {
                        show:'true',
                        trigger: 'item',
                        formatter: '{a} <br/>{b} : {c} ({d}%)',
                    },
                    legend: {
                        orient: 'vertical',
                        left: 'left',
                        data: ['', '', '', '']
                    },
                    series : [
                        {
                            name: '',
                            type: 'pie',
                            radius: '55%',
                            roseType: 'angle',
                            animation: true ,
                            data:initArr,
                            // data:[
                            //     {value:235, name:''},
                            //     {value:274, name:''},
                            //     {value:310, name:''},
                            //     {value:335, name:''},
                            // ]
                        }
                    ]
                })
            }
        }
    }
</script>

<style scoped lang="less">
    #stacked{
        /*padding:50px;*/
    }
</style>